<script>
export default {
  data() {
    return {
      swipe: {},
      index: "",
      aboutUsMsg1:'<h1>ICT战队</h1><strong>重庆邮电大学通信ICT战队</strong>,是明艳老师指导下的，由学生自主管理的本科生学生团体。我们秉承”云聚通信，技战天下”的信念，以传统通信为基础，引导成员学习云计算、大数据等前沿ICT技术。<br/><strong>战队</strong>会定期邀请企业老师到校做各类前沿ICT培训;坚持落实“传帮带”理念，学长学姐手把手带领新成员，在专业技能、综合素质、就业考研等方面进行必要帮助与指导;采用小组学习的形式，定期开展组内外交叉学习，提升大家综合能力。',
      aboutUsMsg2:'<h1>你能得到什么</h1><br/><strong>扎实全面</strong>的ICT知识、技术基础<br/>丰富的比赛经历！<br/>过人的综合能力！！<br/>结识各路大佬！！！<br/>优秀前辈的帮助与指导！！！！<br/>接触大厂的机会，硬核的就业优势！！！！！<br/>此处省略3000字~~o(*≧▽≦)ツ┏━┓',
      gloryMsg1:'<h1>荣誉成果</h1><br/><strong>战队</strong>良好的教学成果得到了华为的认可。2017年11月25日，于通信与信息工程学院通信技术与网络实验中心YF310举行了“华为信息与网络技术网络学院挂牌仪式。<br/><br/><strong>与此同时</strong>战队的优异成绩也受到了学院和学校的关注和肯定，授予了战队通信学院的最高荣誉奖项“通信骄傲”。',
      gloryMsg2:'<strong>战队成立以来</strong>，全体成员累计获得国家级奖项二十余项，省部级奖项五十余项，校级奖项近百项。同时在各种比赛中，战队成员更是硕果累累，斩获华为网络技术大赛全球决赛亚军，摘得“优秀杰出个人”、ICT大赛全国二等奖、深信服网络技术大赛全国冠军......<br/><br/><strong>直至今日</strong>，战队针对成员举办了80余次专业培训和若干次技术交流会，八十余人考取华为网络工程师HCNP认证，通过率达100%。超过1/3的同学进入华为就业。',
      paperPlane:require('../../assets/images/纸飞机.png'),
      requires:[{
        id:'1',
        require:'通信学院18级本科生'
      },
        {
        id:'2',
        require:'有一定自主学习的能力和意识'
        },
        {
        id:'3',
        require:'有一定的表达、沟通交流能力'
        },
        {
        id:'4',
        require:'有长期学习的意愿和准备'
        },
        {
        id:'5',
        require:'有责任心，有团队意识'
        }
      ],
      swiperOption: {
        direction: "vertical",
        on:{
          init:function(){
            let ts = this
            document.addEventListener('touchcancel',function(){
                ts.slideToClosest(500)
            })
          }
        }
      }
    };
  },
  methods: {
  },
  computed: {
    swiper() {
      return this.$refs.mySwiper.swiper;
    }
  },
  watch: {
    "swipe.activeIndex": function() {
      this.index = this.swipe.activeIndex;
    }
  },
  mounted() {
    this.swiper.slideTo(0, 1000, false);
    this.swipe = this.swiper;
  }
};
</script>

